.App {
  font-family: sans-serif;
  text-align: center;
}
.html {
  scroll-behavior: smooth;
  min-height: 100%;
}
#logo {
  width: 15%;
  position: fixed;
  top: 0%;
  left: 0%;
  z-index: 1;
}
#navi p {
  margin-top: 2px;
}
body {
  background-color: black;
}
.nav-bar {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
  color: white;
  display: flex;
  justify-content: flex-end;
  position: fixed;
  top: 12%;
  right: 4%;
  column-gap: 30%;
  z-index: 1;
}
#ham {
  display: none;
}
.nav-bar p {
  white-space: nowrap;
}
label {
  color: lavender;
  font-weight: bold;
}
.resu {
  background-image: url("https://i.pinimg.com/originals/ce/e4/52/cee452e7a99c5571c6ce793f80090c29.jpg");
  padding: 80px 80px;
  padding-bottom: 300px;
  margin: 0;
  background-repeat: no-repeat;
  background-size: cover;
}
.resu form {
  margin-top: 10%;
  margin-left: 30%;
}
.resu button {
  margin-left: 30%;
}
.resu #button2 {
  margin-right: 20%;
}
.nav-bar p:hover {
  text-decoration-line: underline;
  cursor: pointer;
  color: goldenrod;
}
.first {
  background-image: url("https://images4.alphacoders.com/107/thumbbig-1074058.webp");
  background-repeat: no-repeat;
  background-size: cover;
  height: auto;
  background-attachment: fixed;
  padding-bottom: 60%;
}
.card img {
  width: 100%;
}
.card {
  opacity: 1;
  background-color: rgba(0, 0, 0, 0);
  transition: 2s ease-in;
}
.card:hover {
  box-shadow: 10px 10px 5px goldenrod;
  transition-duration: 1s;
}
#delicacy button {
  color: gold;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid gold;
  border-radius: 5px;
}
.delicacy button:hover {
  background-color: gold;
}
.card-title {
  color: gold;
}
.card-text {
  color: white;
}
.catch #dess {
  opacity: 0;
}
.card {
  border: 2px solid gold;
  border-radius: 15px;
  opacity: 0.2;
}
.first #explore {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 8vw;
  white-space: nowrap;
  color: lavender;
  opacity: 0.9;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.order {
  display: flex;
  justify-content: center;
  background-color: red;
  color: lavender;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 300%);
  font-size: 2em;
  padding: 5px 15px;
  border-radius: 5px;
}
.order:hover {
  color: gold;
  border: 2px solid gold;
}
.catch #dess {
  opacity: 1;
  display: block;
  margin-top: 550px;
  background-image: url("https://images7.alphacoders.com/884/thumbbig-884354.webp");
  padding: 80px 80px;
  padding-bottom: 300px;
  padding-left: 10px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  animation: history_reveal 8s ease-in;
  transition-delay: 3s;
}

#prid {
  margin-top: 450px;
  background-image: url("https://i.pinimg.com/originals/ce/e4/52/cee452e7a99c5571c6ce793f80090c29.jpg");
  padding: 80px 80px;
  padding-bottom: 300px;
  padding-left: 2px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  animation: history_reveal 5s ease-in;
  transition-delay: 3s;
}
.catch #prid {
  animation: history_reveal 8s ease-in;
  transition-delay: 3s;
}
#pridet {
  width: 35%;
  padding-bottom: 5rem;
  font-size: 25px;
  opacity: 0.8;
  animation: history_reveal 8s ease-in;
  transition-delay: 3s;
}
.men #newmenu {
  border: 2px solid gold;
  opacity: 1;
  position: relative;
  align-items: center;
  margin-top: 20%;
  left: 25%;
  animation: history_reveal 8s ease-in;
  transition-delay: 3s;
}
#newmenu {
  width: 50%;
  padding-bottom: 5rem;
  font-size: 25px;
  opacity: 0.8;
}
.catch #menu {
  animation: history_reveal 8s ease-in;
  transition-delay: 3s;
}
.col-md-4 #locat {
  padding-top: 12px;
  margin-top: 3px;
}

#dropdownMenuLink {
  background-color: white;
  color: grey;
  opacity: 0.7;
}
.d-flex {
  margin-top: 20px;
  width: 40%;
}
.map {
  display: grid;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  color: white;
  transition: 4s;
  animation: history_reveal 2s;
}
.map {
  margin-top: 100px;
  transition: 4s;
}
.map p {
  font-size: 20px;
  color: gold;
}

.map img {
  width: 100%;
  border: 2px solid gold;
  border-radius: 8px;
}

.menucard img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  position: relative;
  margin-top: 5%;
}

.footer {
  color: gold;
  display: flex;
  justify-content: center;
  column-gap: 95px;
}
.footer p:hover {
  text-decoration: underline;
  cursor: pointer;
}
#about p {
  display: inline-block;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  margin-top: 20%;
}
