#handles img:hover {
  background-color: gold;
  box-shadow: 5px 5px 10px gold;
  opacity: 0.9;
}
#handles img {
  padding: 10px 10px;
  background-color: grey;
  opacity: 0.7;
  width: 2%;
}
#handles p:hover {
  text-decoration: none;
}
@keyframes history_reveal {
  25% {
    display: none;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0.9;
  }
}

@media only screen and (min-width: 765px) {
  .nav-bar {
    column-gap: 10%;
    position: fixed;
    top: 10%;
  }
  .first {
    padding-top: 50%;
  }
  .menucard img {
    width: 45%;
  }
  #handles img {
    width: 4%;
  }
}
@media only screen and (max-width: 765px) {
  #logo {
    width: 20vw;
    z-index: 1;
  }
  .nav-bar {
    position: fixed;
    top: 1%;
    display: grid;
    column-gap: 2px;
    overflow-x: hidden;
  }
  .order {
    position: absolute;
    top: 50%;
    left: 50%;
  }
  #about p {
    display: inline-flex;
    margin-top: 0%;
  }
}
@media (min-width: 320px) and (max-width: 764px) {
  .first #front #explore {
    position: absolute;
    font-size: 15vw;
  }
  #ham {
    display: inline;
    z-index: 1;
    border: 2px solid gold;
    border-radius: 7px;
    background-color: black;
    color: gold;
  }
  #ham {
    position: fixed;
    width: 12%;
    top: 3%;
    right: 2%;
    background-size: cover;
    background-color: black;
    cursor: pointer;
  }
  .nav-bar {
    position: fixed;
    top: 9%;
    right: 2%;
    background-color: black;
    padding: 4% 5%;
    border: 2px solid gold;
    border-radius: 7px;
    z-index: 1;
    animation: history_reveal 2s;
  }
  .nav {
    display: none;
  }
  .first {
    background-image: url("https://images4.alphacoders.com/107/thumbbig-1074058.webp");
    background-position: center;
    background-size: cover;
    padding-top: 100vw;
    overflow-x: hidden;
  }
  .catch #dess {
    background-image: url("https://images7.alphacoders.com/884/thumbbig-884354.webp");
    background-position: center;
    background-size: cover;
    padding: 0;
  }
  .order {
    position: absolute;
    padding: 2px 4px;
    font-size: 5vw;
  }
  #prid {
    background-position: center;
    overflow-x: hidden;
  }
  #pridet {
    width: 100%;
  }
  .men {
    width: 100%;
    position: relative;
    right: 10%;
  }
  #newmenu {
    width: 75%;
  }
  .menucard img {
    position: relative;
    left: 10%;
    width: 100%;
  }
  #handles img {
    width: 10vw;
  }
}
